,yBAAA;EACA,gBAAA;CACA;AACA;EACA,wBAAA;CACA;AACA;EACA,mBAAA;EACA,iBAAA;EACA,iBAAA;CACA;AAEA;EACA,eAAA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,yBAAA;MAAA,sBAAA;UAAA,wBAAA;CACA;AACA;CAGA;AACA;EACA,mBAAA;EACA,kBAAA;EACA,kBAAA;EACA,aAAA;EACA,cAAA;EACA,mBAAA;CACA;AACA;EACA,aAAA;EACA,uBAAA;EACA,YAAA;EACA,cAAA;EACA,6BAAA;CACA;AACA;GACA,kBAAA;CACA;AACA;EACA,eAAA;EACA,YAAA;EACA,YAAA;CACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,gBAAA;EACA,eAAA;CACA","file":"Reject.vue","sourcesContent":["<template>\n  <div class='adopt' ref='adopt'>\n    <table class='table'>\n      <tr>\n        <th>用户名称</th>\n        <th>电话</th>\n        <th>车型</th>\n        <th>车牌</th>\n        <th>设备号</th>\n        <th>保单号</th>\n        <th>驳回理由</th>\n        <th>申请时间</th>\n        <th>驳回时间</th>\n        <th>详情</th>\n      </tr>\n      <tr v-for=\"item in list\">\n        <td>{{item.name}}</td>\n        <td>{{item.phone}}</td>\n        <td>{{item.type}}</td>\n        <td>{{item.plate}}</td>\n        <td>{{item.eq_num}}</td>\n        <td>{{item.policy_num}}</td>\n        <td>\n          <a href=\"javascript:;\" @click='reason=item.reason,reasonVisible=true'>查看</a>\n        </td>\n        <td>{{item.create_time}}</td>\n        <td>{{item.audit_time}}</td>\n        <td>\n          <a href=\"javascript:;\" @click='details(item.pid,item.u_id)'>详情</a>\n        </td>\n      </tr>\n    </table>\n    <!--详情弹框-->\n    <el-dialog title=\"详情\" :visible.sync=\"detailsVisible\" v-drag width=\"800px\" center>\n      <div class='details'>\n        <div class='content'>\n          <!--保单信息-->\n          <dl class='information'>\n            <dt>保单信息:</dt>\n            <dd class='left'>\n              <table class='table'>\n                <tr>\n                  <td>保险公司 : {{information.company}}</td>\n                  <td>保单号 : {{information.policy_num}}</td>\n                  <td>VIN码 : {{information.vin}}</td>\n                </tr>\n                <tr>\n                  <td>开始时间 : {{information.start_time}}</td>\n                  <td>结束时间 : {{information.end_time}}</td>\n                </tr>\n              </table>\n            </dd>\n          </dl>\n          <!--险种详情-->\n          <div class='Insurancetype'>\n            <table>\n              <tr>\n                <th>险种名称</th>\n                <th>金额</th>\n              </tr>\n              <tr v-for=\"item in information.name_price\">\n                <td>{{item.type}}</td>\n                <td>{{item.value}}</td>\n              </tr>\n            </table>\n          </div>\n          <div class='total'>\n            <span class='right'>保单总金额:{{information.total}}</span>\n          </div>\n          <!--保单照片-->\n          <div class='Photo'>\n            <ul>\n              <li v-for='(item,key) in information.pc_img'><img :src=\"item\" @click=\"ImgDialogVisible=true,ImgList=item,enlarge()\"></li>\n            </ul>\n          </div>\n        </div>\n      </div>\n    </el-dialog>\n    <el-dialog title=\"驳回理由\"  :visible.sync=\"reasonVisible\" v-drag width=\"800px\" center>\n           <div class='reasonContent'>\n                 {{reason}}\n           </div>\n    </el-dialog>\n    <!--分页组件-->\n    <div class=\"page_center\">\n      <paging :page-count=\"pageCount\"   :page=\"page\" @index=\"paging\"></paging>\n    </div>\n  </div>\n</template>\n<script>\n  import Viewer from 'viewerjs';\n  import 'viewerjs/dist/viewer.css';\n  export default{\n    data(){\n      return{\n        token:window.sessionStorage.getItem('bbytoken'),\n        list:{},\n        page:1, //当前页\n        pageCount:1,//总页数\n        detailsVisible:false, //详情弹框状态\n        ReasonVisible:false, //驳回理由状态\n        ImgDialogVisible:false,\n        reasonVisible:false,\n        information:{}, //保单信息\n        ImgList:'',\n        transform:{\n          transform:''\n        },\n        rotateData:0,\n        iconfont:false,\n        reason:''\n      }\n    },\n    directives: {\n      drag: {\n        // 指令的定义\n        bind: function (el) {\n          //获取当前元素\n          var odiv=el.querySelector('.el-dialog')\n          odiv.onmousedown = (e) => {\n            //算出鼠标相对元素的位置\n            let disX = e.clientX - odiv.offsetLeft;\n            let disY = e.clientY - odiv.offsetTop;\n\n            document.onmousemove = (e)=>{\n              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置\n              let left = e.clientX - disX;\n              let top = e.clientY - disY;\n\n              //绑定元素位置到positionX和positionY上面\n//              this.positionX = top;\n//              this.positionY = left;\n              //移动当前元素\n              odiv.style.marginLeft = left+\"px\"\n              odiv.style.marginTop = top + 'px';\n            };\n            document.onmouseup = (e) => {\n              document.onmousemove = null;\n              document.onmouseup = null;\n            };\n          };\n        }\n      }\n    },\n    methods:{\n      paging(e){\n        this.page=e;\n        this.init()\n      },\n      init(){\n        this.$axios.post('admin/PolicyAudit/rejList',{token:this.token,page:this.page}).then(res=>{\n          if(res.data.code==1){\n         \n          this.list=res.data.data.list\n          this.pageCount=res.data.data.rows;\n        }else{\n          layer.msg(res.data.msg,{time:1500})\n        }\n      }).catch(err=>console.log(err))\n      },\n      //查看未审核列表详情\n      details(pid,uid){\n        \n        this.UnauditedID={\n          token:this.token,\n          pid:pid,\n          uid:uid\n        }\n        this.detailsVisible=true\n        this.$axios.post('admin/PolicyAudit/detail',{token:this.token,pid:pid}).then(res=>{\n          if(res.data.code==1){\n          this.information=res.data.data\n        }\n      }).catch(err=>console.log(err))\n      },\n      enlarge(){ //图片放大\n        const ViewerDom = this.$refs.adopt\n        const viewer = new Viewer(ViewerDom, {});\n      }\n\n    },\n    mounted(){\n      this.init()\n    }\n  }\n</script>\n<style scoped>\n  >>> .viewer-canvas{\n    z-index: 9999;\n  }\n  >>> .el-dialog__wrapper{\n        overflow: hidden;\n      }\n  .table a{\n    color: #04BBFC;\n  }\n  .details{\n    font-size: 12px;\n  }\n  .information td{\n      text-align: left;\n  }\n  .details table td{\n    font-size: 12px;\n    border: none;\n  }\n  .Insurancetype{\n    margin-top: 20px;\n  }\n  .Insurancetype table{\n    border:1px solid #DBDBDB\n  }\n  .Insurancetype table th{\n    background: none;\n    width: 50%;\n    border:1px solid #DBDBDB;\n    font-size: 16px;\n  }\n  .Insurancetype table td{\n    border:1px solid #DBDBDB\n  }\n  .Photo{\n    text-align: center;\n    margin-top: 20px;\n    overflow-x: auto;\n  }\n\n  .Photo>ul{\n    margin: 0 auto;\n    display: flex;\n    justify-content: center;\n  }\n  .Photo>ul>li{\n\n\n  }\n  .Photo img{\n    text-align: center;\n    max-height: 100px;\n    max-height: 100px;\n    width: 100px;\n    height: 100px;\n    padding-left: 10px;\n  }\n  .content>textarea{\n    border: none;\n    width: 100% !important;\n    resize:none;\n    height: 100px;\n    max-height: 300px !important;\n  }\n  .reasonContent{\n     min-height: 300px;\n  }\n  .total:after{\n    display: block;\n    content: '';\n    clear: both;\n  }\n\n  .right{\n    float: right;\n    margin: 20px 0;\n    font-size: 18px;\n    color: #04BBFC;\n  }\n</style>\n<style>\n  .adopt .el-dialog__header{\n    background: none;\n    padding-bottom: 20px;\n    border-bottom: 1px solid #E6E6EB;\n  }\n  .adopt .el-dialog__footer{\n    border-top: 1px solid #E6E6EB;\n    padding-bottom: 10px;\n  }\n  .Reason .el-dialog__header{\n\n    padding-bottom: 20px;\n    border-bottom: 1px solid #E6E6EB;\n  }\n  .Reason .el-dialog__footer{\n    border-top: 1px solid #E6E6EB;\n    padding-bottom: 10px;\n  }\n</style>\n"],"sourceRoot":""}]);

